}
.directorist-dropdown-select-toggle:before {
  content: "";
  position: absolute !important;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.directorist-dropdown-select-items {
  position: absolute;
  width: 100%;
